<template>
  <div id="make_money">
    <div class="bgBox">
      <div class="box box1">
        <p class="p1"></p>
        <p class="p2">7%</p>
      </div>
      <div class="box box2">
        <p class="p1">TA</p>
        <p class="p2">6%</p>
      </div>
      <div class="box box3">
        <p class="p1">TA</p>
        <p class="p2">5%</p>
      </div>
    </div>
    <!---->
    <!-- <div class="marquee_box">
      <ul class="marquee_list" :style="{ top: -Num + 'px'}" :class="{marquee_top:Num}">
         Num=0false-->
    <!-- <li v-for="(lists,i) in list" :key="i">
      <p>{{lists.username}}{{lists.num}}{{lists.sumNum}}</p>
    </li>
    </ul>
  </div> -->

    <!--  -->
    <div class="infoMsg">
      <p class="infoTitle">
        <span class="red"></span>
        <span> </span>

      </p>
      <p class="infoMy">123.84</p>
      <p class="infoEmp">688</p>
      <ul>
        <li> 7%48.16</li>
        <li> Ta6%41.28</li>
        <li> TaTa5%34.4</li>
      </ul>
    </div>
    <!---->
    <div class="share" @click="shareFrend"></div>
    <p class="check" @click="goRecord"></p>

    <!--  -->
    <div class="Meng" id="box" v-if="isShow" @click="hideToggle"></div>
    <img src="../../img/shareHint.png" alt="" class="shareHint" v-if="isShow" @click="hideToggle">
  </div>
</template>

<script>
import axios from "axios";
import qs from "qs";
import wx from "weixin-js-sdk";
export default {
  name: "make_money",
  data() {
    return {
      list: [],
      num: "",
      sumNum: "",
      username: "",
      Num: 0,
      isShow: false,
      sendOpenid: "",
      marqueeList: [
        {
          name: "1",
          city: "",
          amount: "10"
        },
        {
          name: "2",
          city: "",
          amount: "20"
        },
        {
          name: "3",
          city: "",
          amount: "30"
        },
        {
          name: "4",
          city: "",
          amount: "40"
        }
      ]
    };
  },
  methods: {
    //
    goBack() {
      this.$router.push({ path: "/" });
    },
    hideToggle() {
      this.isShow = false;
    },
    //
    goRecord() {
      this.$router.push({ path: "/Invited_record" });
    },
    showMarquee: function(Num) {
      this.marqueeList.push(this.marqueeList[0]);
      var max = this.marqueeList.length;
      var that = this;
      var marqueetimer = setInterval(function() {
        Num++;
        if (Num >= max) {
          Num = 0;
        }
        that.Num = Num * 30;
      }, 1000);
    },
    //
    getNoticeList() {
      var $this = this;
      var baseUrl = BaseUrl + "api/getIncomeList";
      var data = qs.stringify({
        openid: localStorage.getItem("openid")
      });
      axios({
        method: "post",
        url: baseUrl,
        type: "json",
        data: data
      })
        .then(function(data) {
          console.log(data);
          let datas = data.data.data;
          if (data.data.status == 1) {
            $this.list = datas;
          } else {
            Toast({
              message: datas.msg,
              duration: 1500
            });
          }
        })
        .catch(function() {
          //alert("")
        });
    },
    getQueryString(name, url) {
      url = url || window.location.search.substr(1);
      console.log(url);
      var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
      var r = url.match(reg);
      if (r != null) return decodeURI(r[2]);
      return null;
    },
    // lyt
    WxZd() {
      //
      const openid = localStorage.getItem("openid");

      var data = qs.stringify({
        originUrl: window.location.href
      });
      console.log(data);
      var baseUrl = BaseUrl + "index/getSignPackage";
      axios({
        method: "POST",
        url: baseUrl,
        type: "json",
        data: data
      }).then(
        function(res) {
          var $this = this;
          var info = res.data.data.signPackage;
          this.appId = info.appId;
          this.nonceStr = info.nonceStr;
          this.timestamp = info.timestamp;
          this.signature = info.signature;
          wx.config({
            debug: false,
            appId: this.appId, // Ticke------
            timestamp: this.timestamp, // 
            nonceStr: this.nonceStr, // 
            signature: this.signature, // 1
            //:
            jsApiList: ["onMenuShareAppMessage", "onMenuShareTimeline"]
          });

          //
          wx.error(function(res) {
            // logUtil.printLog(":", res);
            console.log(res);
          });
          wx.ready(function() {
            // 
            wx.onMenuShareAppMessage({
              title: "", // 
              desc: "", // 
              link:
                "http://egg.wufuapp.com/friends_money_details?openid=" +
                $this.sendOpenid +
                "&makeMoney=100&onlyMine=100",
              imgUrl:
                "http://biuimage.oss-cn-qingdao.aliyuncs.com/uploads/ueditor/image/20180912/1536745700851746.png", // 
              type: "", // ,musicvideolinklink
              dataUrl: "", // typemusicvideo
              success: function() {
                console.log("");
              },
              cancel: function() {
                console.log("");
                // 
              },
              fail: function(res) {
                window.alert(JSON.stringify(res));
              }
            });
            // 
            wx.onMenuShareTimeline({
              title: "", // 
              link:
                "http://egg.wufuapp.com/friends_money_details?openid=" +
                $this.sendOpenid +
                "&makeMoney=100&onlyMine=100",
              imgUrl:
                "http://biuimage.oss-cn-qingdao.aliyuncs.com/uploads/ueditor/image/20180912/1536745700851746.png", // 
              success: function() {
                console.log("");
              },
              cancel: function() {
                console.log("");
              },
              fail: function(res) {
                window.alert(JSON.stringify(res));
              }
            });
          });
        }.bind(this)
      );
    },
    shareFrend() {
      // alert(1);
      this.isShow = true;
    }
  },
  mounted() {
    document.title = "";

    this.sendOpenid = localStorage.getItem("openid");

    // this.getNoticeList();
    this.showMarquee(this.Num);
    this.WxZd();
  },
  created() {},
  components: {}
};
</script>

<style lang="scss" scoped>
#make_money {
  background: #e9844d;
  min-height: 43rem;
  .mint-header {
    background: #ffffff;
    color: #000;
  }
  .makebg {
    width: 100%;
    display: block;
    margin-top: -0.5rem;
  }
  .bgBox {
    width: 100%;
    background: url("http://biuimage.oss-cn-qingdao.aliyuncs.com/uploads/ueditor/image/20180928/1538128038482726.png")
      no-repeat;
    background-size: 100% 100%;
    -moz-background-size: 100% 100%;
    -webkit-background-size: 100% 100%;
    display: inline-block;
    .box {
      width: 100%;
      .p1 {
        color: #86411c;
        font-size: 1rem;
      }
      .p2 {
        color: #f3e926;
        font-size: 1.1rem;
      }
    }
    .box1 {
      width: 7rem;
      padding-left: 9.5rem;
      display: inline-block;
      margin-top: 12.4rem;
    }
    .box2 {
      width: 7rem;
      padding-left: 65%;
      margin-top: 2.5rem;
    }
    .box3 {
      margin-top: 3.8rem;
      text-align: right;
      width: 40%;
    }
  }
  /**/
  .roller_announcement {
    text-align: center;
    width: 76%;
    height: 3.5rem;
    font-size: 0.9rem;
    color: #662908;
    margin: 1rem auto;
    line-height: 1.2rem;
  }

  .marquee_box {
    display: block;
    position: relative;
    width: 100%;
    text-align: center;
    color: #662908;
    margin: 1rem auto;
    height: 4rem; /**/
    overflow: hidden;

    ul {
      list-style: none;
      width: 100%;
    }
    .marquee_list {
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      padding: 0;
    }
  }

  .infoMsg {
    text-align: center;
    background-color: #f1d52e;
    margin: 1rem;
    border-radius: 0.5rem;
    padding: 0.1rem 0.5rem 1rem;
    .infoTitle {
      position: relative;
      height: 3rem;
      text-decoration: line-through;
      span {
        font-size: 13px;
        position: absolute;
        width: 50%;
        left: 50%;
        transform: translate(-50%);
        top: 20%;
        line-height: 2rem;
        background-color: #f1d52e;
      }
      .red {
        width: 100%;
        height: 0.1rem;
        position: absolute;
        left: 50%;
        top: 50%;
        background-color: #ef3c67;
      }
    }
    .infoMy {
      font-size: 24px;
      color: #ea6f2c;
    }
    .infoEmp {
      font-size: 14px;
      color: #2e0406;
      line-height: 2rem;
    }
    ul {
      text-align: left;
      list-style: none;
      padding: 0;
      margin: 0;
      li {
        color: #2e0406;
        line-height: 1.5rem;
        font-size: 14px;
      }
    }
  }
  /**/
  .share {
    width: 13.5rem;
    font-size: 1.3rem;
    line-height: 3.5rem;
    background: #ef3c69;
    color: #fff;
    border-radius: 2rem;
    margin: 0 auto;
    text-align: center;
  }
  .check {
    width: 30%;
    text-align: center;
    margin: 0 auto;
    margin-top: 0.5rem;
    color: #954c24;
    padding-bottom: 1rem;
    font-size: 0.9rem;
    text-decoration: underline;
  }
  .Meng {
    opacity: 0.56;
    background: #000;
    z-index: 9;
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
  }
  .shareHint {
    width: 80%;
    margin: 0 auto;
    display: block;
    z-index: 999;
    position: fixed;
    top: 3rem;
    left: 3.3rem;
  }
}
</style>



