<script>
import { Navbar, TabItem } from "mint-ui";
import axios from "axios";
import qs from "qs";
import { Toast } from "mint-ui";
import { MessageBox } from "mint-ui";
export default {
  name: "myOrder",
  data() {
    return {
      openid: "",
      id: "", //id
      selected: "",
      packageInfo: [],
      allList: [],
      obligationList: [], //tab 
      receivingList: [], //tab 
      evaluateList: [], //tab 
      quanbu: [], //
      goodsIdAll: "",
      packageIdAll: "",
      numberAll: "",
      totalPriceAll: "",
      aidAll: "",
      orderSnAll: ""
    };
  },
  methods: {
    getOrderDetail(id) {
      this.$router.push({
        path: "/order_details",
        query: {
          orderId: id
        }
      });
    },
    //
    getMyOrder() {
      var $this = this;
      var baseUrl = BaseUrl + "/api/getOrderList";
      var data = qs.stringify({
        openid: localStorage.getItem("openid"),
        // openid: "oX6js0QSdvyFy_Yhjkk3upHHR_e0",
        type: $this.selected
      });
      axios({
        method: "post",
        url: baseUrl,
        type: "json",
        data: data
      }).then(function(data) {
        console.log(data, 111111);
        let datas = data.data.data;
        $this.id = datas[0].id; //id
        if (data.data.status == 1) {
          $this.allList = datas;
          for (var i in datas) {
            $this.quanbu.push(datas[i].status); //
            if (datas[i].status == "") {
              datas[i].status = "";
              $this.obligationList.push(datas[i]);
              console.log($this.obligationList);
            } else if (datas[i].status == "") {
              datas[i].status = "";
              $this.receivingList.push(datas[i]);
            } else if (datas[i].status == "") {
              datas[i].status = "";
              $this.receivingList.push(datas[i]);
            } else {
              datas[i].status = "";
              $this.evaluateList.push(datas[i]);
            }
          }
        } else {
          Toast({
            message: datas.msg,
            duration: 1500
          });
        }
      });
    },
    //
    nextPage(item) {
      var $this = this;
      $this.login(item, 1);
      this.$router.push({
        path: "/package_details",
        query: {
          id: item.status,
          totalPrice: $this.totalPriceAll,
          goodsName: item.packageInfo.goodsName,
          orderSn: item.orderSn,
          packageId: item.packageId,
          orderids: item.id,
          remark: item.remark,
          amount: item.number,
          openid: item.openid,
          goodsId: item.goodsId,
          orderSn: item.orderSn
        }
      });
      console.log(item);
    },
    nextStay(item) {
      this.$router.push({
        path: "/package_details",
        query: {
          id: item.status,
          openid: item.openid,
          orderids: item.id,
          remark: item.remark
        }
      });
      console.log(item);
    },
    details1() {
      this.$router.push({
        path: "/order_details",
        query: {
          id: "",
          orderId: this.id
        }
      });
    },
    details2() {
      this.$router.push({
        path: "/order_details",
        query: {
          id: "",
          orderId: this.id
        }
      });
    },
    reqAddress() {
      this.$router.push({
        path: "/Fullcreate_Address",
        query: {
          openid: localStorage.getItem("openid"),
          orderId: receivingList[i].id //
        }
      });
    },
    templates(i, receivingList) {
      var $this = this;
      var baseUrl = "/api/api/channelOrder";
      var data = qs.stringify({
        openid: localStorage.getItem("openid"),
        id: receivingList[i].id
      });
      axios({
        method: "post",
        url: baseUrl,
        type: "json",
        data: data
      })
        .then(function(data) {
          console.log(data);
          let datas = data.data.data;
          if (data.data.status == 1) {
            Toast({
              message: data.data.msg,
              duration: 2000
            });

            $this.set(receivingList.splice(i, i++));
          } else {
            Toast({
              message: datas.msg,
              duration: 1500
            });
          }
        })
        .catch(function() {
          //alert("")
        });
    },

    cancellation(i) {
      MessageBox.confirm("?").then(action => {
        this.templates(i, this.obligationList);
      });
    },
    cancellations(i) {
      MessageBox.confirm("?").then(action => {
        this.templates(i, this.receivingList);
      });
    },
    deletes(i) {
      MessageBox.confirm("?").then(action => {
        var $this = this;
        var baseUrl = "/api/api/delOrder";
        var data = qs.stringify({
          openid: localStorage.getItem("openid"),
          id: $this.evaluateList[i].id
        });
        axios({
          method: "post",
          url: baseUrl,
          type: "json",
          data: data
        })
          .then(function(data) {
            console.log(data);
            let datas = data.data.data;
            if (data.data.status == 1) {
              Toast({
                message: data.data.msg,
                duration: 1500
              });

              $this.set($this.evaluateList.splice(i, i++));
            } else {
              Toast({
                message: datas.msg,
                duration: 1500
              });
            }
          })
          .catch(function() {
            //alert("")
          });
      });
    },

    //
    login(item, num) {
      //
      console.log(num);
      var $this = this;
      var baseUrl = BaseUrl + "api/preOrder";
      var data = qs.stringify({
        goodsId: item.goodsId,
        packageId: item.packageId,
        number: item.number,
        openid: item.openid
      });

      axios({
        method: "post",
        url: baseUrl,
        type: "json",
        data: data
      }).then(function(data) {
        //console.log(data);
        let datas = data.data.data;
        if (data.data.status == 1) {
          $this.goodsIdAll = datas.goodsId;
          $this.packageIdAll = datas.packageId;
          $this.numberAll = datas.number;
          $this.totalPriceAll = datas.totalPrice;
          $this.aidAll = datas.address;

          if (num == 0) {
            $this.goPay(item);
          }
        }
      });
    },
    goPay(item) {
      var $this = this;
      axios
        .get(
          "http://wufuapp.com/index.php/api_egg/Weixinpay/index?openid=" +
            item.openid +
            "&price=" +
            $this.totalPriceAll +
            "&remark=" +
            item.packageInfo.goodsName +
            "&orderSn=" +
            item.orderSn
        )
        .then(function(data) {
          //console.log(item.orderSn);
          let datas = data.data.data;
          if (data.data.status == 1) {
            $this.orderSnAll = item.orderSn;
            $this.onBridgeReady(datas);
          } else {
            Toast({
              message: "",
              duration: 1500
            });
          }
        });
    },
    onBridgeReady(data) {
      var $this = this;

      //

      WeixinJSBridge.invoke(
        "getBrandWCPayRequest",
        {
          appId: data.appId, //
          timeStamp: data.timeStamp, //1970
          nonceStr: data.nonceStr, //
          package: data.package,
          signType: "MD5", //
          paySign: data.paySign //
        },
        function(res) {
          if (res.err_msg == "get_brand_wcpay_request:ok") {
            axios
              .get(
                "http://wufuapp.com/index.php/api_egg/Weixinpay/H5Notify?out_trade_no=" +
                  $this.orderSnAll
              )
              .then(function(data) {
                $this.$router.push({
                  path: "pay_success",
                  query: { orderId: $this.orderId, money: $this.totalPriceAll }
                });
              })
              .catch(function() {
                //alert("")
              });

            //
          }
        }
      );
    }
  },
  mounted() {
    this.getMyOrder();
  },
  components: {}
};
</script>

<style lang="scss" scoped>
#myOrder {
  background: #f0f0f2;
  min-height: 41.6rem;
  .mint-header {
    background: #cc3e36;
  }
  .mint-navbar {
    margin-top: 3rem;
  }
  .mint-navbar .mint-tab-item.is-selected {
    color: #cc3e36;
    border-bottom: none;
  }
  .orderBox {
    margin-top: 0.5rem;
    background: #ffffff;
    padding: 0.5rem;
    padding-right: 0;

    .orderNum {
      font-size: 12px;
      color: #79797b;
      margin-bottom: 10px;
      span {
        margin-left: 5px;
      }
    }
    .goodsInfo {
      display: flex;
      align-items: center;
      border-bottom: 1px solid #c1c5c8;
      padding-bottom: 0.5rem;
      img {
        width: 20%;
      }
      .name {
        width: 80%;
        padding-left: 0.8rem;
        color: #252424;
        position: relative;
        .money {
          display: flex;
          align-items: center;
          .jine {
            position: absolute;
            width: 30%;
            right: 1rem;
            top: 0.5rem;
            z-index: 9;
            text-align: right;
          }
          .shul {
            position: absolute;
            width: 20%;
            right: 1rem;
            top: 2.5rem;
            text-align: right;
            z-index: 7;
          }
        }
      }
    }
    .total {
      text-align: right;
      color: #252424;
      font-size: 0.75rem;
      p {
        padding-top: 1.0625rem;
        padding-right: 0.5rem;
        span {
          color: #cc3e36;
        }
      }
    }
    .btns {
      font-size: 0.75rem;
      display: flex;
      align-items: right;
      justify-content: flex-end;
      text-align: right;
      height: 2.125rem;
      line-height: 3.625rem;
      margin-top: 0.8rem;
      .cancelBtn {
        width: 5rem;
        line-height: 1.6rem;
        text-align: center;
        color: #79797b;
        border: 1px solid #79797b;
        border-radius: 2rem;
        margin-right: 0.8rem;
      }
      .goPay {
        width: 5rem;
        line-height: 1.6rem;
        text-align: center;
        color: #cc3e36;
        border: 1px solid #cc3e36;
        border-radius: 2rem;
        margin-right: 0.8rem;
      }
      .flag {
        position: absolute;
        left: 1rem;
        text-align: center;
        font-size: 0.75rem;
        color: #cc3e36;
        width: 3rem;
        height: 2rem;
        line-height: 0.8rem;
      }
    }
  }
}
</style>



