<template>
    <div id="Brought_egg_error">
      <!---->
      <div class="hints">
        <p class="congratulation">~</p>
        <p>TaTa30</p>
      </div>
      <div class="friedsItem">
        <img src="../../img/touxiang.png" alt="">
        <p>30</p>
      </div>
      <div class="help" @click="goShare" v-model="share">{{share}}</div>
      <!--<div class="help" @click="goShare"></div>-->

      <!---->
      <div class="box" id="box" v-show="isShow" @click="hideToggle"></div>
      <img src="../../img/shareHint.png" alt="" class="shareHint" v-show="isShow" @click="hideToggle">
    </div>
</template>

<script>
    export default {
        name:'Brought_egg_error',
        data(){
          return{
            isShow:false,
            share:'',
          }
        },
        methods:{
          //
          hideToggle() {
            this.isShow = !this.isShow;
          },
          goShare(){
            // 0,
            if('0'){
              this.share='';
              this.isShow = !this.isShow;
            }
          },
        },
        created(){
          document.title="";
        },
        mounted(){},
        components:{}
    }
</script>

<style lang="scss" scoped>
#Brought_egg_error{
  background: #a6ffd7;
  min-height: 41.7rem;
  .hints{
    padding-top: 3rem;
    width: 90%;
    margin: 0 auto;
    font-size: 1.3rem;
    line-height: 2rem;
    text-align: center;
    .congratulation{
      color: #CC3E36;
    }
    p{
      color: #420909;
    }
  }
  .friedsItem{
    width:100%;
    margin-top: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border-top: 1px solid #dfdfdf;
    img{
      width:3rem;
      height:2.5rem;
      display: block;
      margin-left: .5rem;
    }
    p{
      font-size: 1rem;
      padding: 0.5rem;
    }
  }
  /**/
  .help {
    width: 50%;
    line-height: 3.56rem;
    background: rgba(255, 176, 17, 1);
    border-radius: 3rem;
    color: #2e0406;
    text-align: center;
    margin: 0 auto;
    margin-top: 5rem;
  }
  /**/
  .box {
    opacity: 0.56;
    background: #000;
    z-index: 9;
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    /*display:none*/
  }
  .shareHint {
    width: 80%;
    margin: 0 auto;
    display: block;
    z-index: 999;
    position: absolute;
    top: 3rem;
    left: 3.3rem;
  }
}
</style>



