<template>
  <div id="ganenAct">
    <div class="progressOne" >
        <div class="imgPic">
          <img src="../../img/ganenH.jpg" alt="">
        </div>
        <div  class="descInfo">
          <p><br/>
               <span style="padding-left: 20px"> A</span><br/>
                <span style="padding-left: 20px">B:  </span><br/>
                <span style="padding-left: 20px">C</span><br/>
                <span style="padding-left: 20px">D</span><br/>
               <span style="padding-left: 20px"> E</span>
                .....50</p>
              <p> </p>
              <p>'<span style="color:red">1</span>'</p>
        </div>
       <div class="descInfoF">
          <p>: 11201122</p>
          <!-- <p>: 22</p> -->
          <p>18017116456</p>
          <p>: 021-51853776</p>
          <p>: </p>
          <p>428A6#216</p>
       </div>
        <div class="fx splitY">
          <div class="buttOne redCol" @click="sendShare"></div>
        </div>
        <div class="bottomIMg">
           <p class="Address"></p>
            <div class="logoPic">
              <img src="../../img/logo.png" alt="">
            </div>
        </div>
        
    </div>
    <!--  -->
      <div class="box" id="box" @click="isShow=false" v-if="isShow"></div>
      <img src="../../img/shareHint.png" @click="isShow=false"  class="shareHint" v-if="isShow">
  </div>
</template>
<script>
import { Header } from "mint-ui";
import axios from "axios";
import qs from "qs";
import { Toast } from "mint-ui";
import { Indicator } from "mint-ui";

import utils from "../common/common.js";

export default {
  name: "product_details",
  data() {
    return {
      openid: "",
      activityIdMine: "",
      isShow: false
    };
  },
  methods: {
    //+openid
    login: function() {
      var $this = this;
      var data = qs.stringify({
        paramUrl: window.location.href
      });
      var baseUrl = BaseUrl + "index/getCode";
      axios({
        method: "POST",
        url: baseUrl,
        type: "json",
        data: data
      }).then(function(data) {
        let datas = data.data.data;
        location.href = datas.url;
      });
    },
    //openid
    logins: function() {
      var $this = this;
      var baseUrl = BaseUrl + "index/getUserOpenid";
      var data = qs.stringify({
        code: utils.getQueryString("code")
      });
      axios({
        method: "post",
        url: baseUrl,
        type: "json",
        data: data
      }).then(function(data) {
        if (data.data.status == 1) {
          $this.openid = data.data.data.openid;
          localStorage.setItem("openid", $this.openid);
        }
      });
    },
    //
    bindRela() {
      var $this = this;
      var data = qs.stringify({
        activtyId: $this.getQueryString("activityIdOther"),
        openid: localStorage.getItem("openid")
      });
      var baseUrl = BaseUrl + "index/bindRelation";
      axios({
        method: "POST",
        url: baseUrl,
        type: "json",
        data: data
      }).then(function(data) {
        console.log(data);
      });
    },
    getQueryString(name, url) {
      url = url || window.location.search.substr(1);
      console.log(url);
      var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
      var r = url.match(reg);
      if (r != null) return decodeURI(r[2]);
      return null;
    },
    //id
    getAct() {
      var that = this;
      var data = qs.stringify({
        openid: localStorage.getItem("openid")
      });
      axios({
        url: BaseUrl + "/index/personalCenter",
        method: "post",
        data: data,
        type: "json"
      }).then(info => {
        var data = info.data.data;
        that.activityIdMine = data.myActivityId;
        that.WxZd();
      });
    },
    WxZd() {
      var $this = this;
      var data = qs.stringify({
        originUrl: window.location.href
      });
      var baseUrl = BaseUrl + "index/getSignPackage";
      axios({
        method: "POST",
        url: baseUrl,
        type: "json",
        data: data
      }).then(
        function(res) {
          var info = res.data.data.signPackage;
          this.appId = info.appId;
          this.nonceStr = info.nonceStr;
          this.timestamp = info.timestamp;
          this.signature = info.signature;
          wx.config({
            debug: false,
            appId: this.appId, // Ticke------
            timestamp: this.timestamp, // 
            nonceStr: this.nonceStr, // 
            signature: this.signature, // 1
            //:
            jsApiList: ["onMenuShareAppMessage", "onMenuShareTimeline"]
          });
          //
          wx.error(function(res) {
            console.log(res);
          });
          //
          wx.ready(function() {
            //
            wx.onMenuShareTimeline({
              title: "~!", // 
              link:
                "http://egg.wufuapp.com/ganenAct?openid=" +
                $this.openid +
                "&activityIdOther=" +
                $this.activityIdMine,
              imgUrl: "http://egg.wufuapp.com//static/img/ganenAct.jpg", // 
              success: function(res) {},
              cancel: function(res) {
                console.log(res);
              }
            });
            //
            wx.onMenuShareAppMessage({
              title: "~!", // 
              desc: "", // 
              link:
                "http://egg.wufuapp.com/ganenAct?openid=" +
                $this.openid +
                "&activityIdOther=" +
                $this.activityIdMine,
              imgUrl: "http://egg.wufuapp.com//static/img/ganenAct.jpg", // 
              type: "", // ,musicvideolinklink
              dataUrl: "", // typemusicvideo
              success: function(res) {},
              cancel: function(res) {
                console.log(res);
              }
            });
          });
        }.bind(this)
      );
    },
    sendShare() {
      this.isShow = true;
    }
  },
  created() {
    if (!localStorage.getItem("openid")) {
      if (utils.getQueryString("code")) {
        this.logins();
      } else {
        this.login();
      }
    }
    if (utils.getQueryString("activityIdOther")) {
      this.bindRela();
      this.WxZd();
    } else {
      this.getAct();
    }
  },
  mounted() {}
};
</script>
<style lang="scss" scoped>
#ganenAct {
  position: relative;
  height: 100%;
  .imgPic {
    img {
      width: 100%;
    }
  }

  .descInfo {
    margin: 0.65rem;
    font-size: 14px;
    line-height: 1.6rem;
  }
  .descInfoF {
    margin: 0.65rem;
    font-size: 14px;
    line-height: 1.6rem;
  }
  .Address {
    font-size: 14px;
    text-align: center;
  }
  .bottomIMg {
    width: 100%;
    height: 3rem;
    margin-top: 1rem;
    color: #dcdddd;
    .logoPic {
      width: 35%;
      margin: 0 auto;
      img {
        width: 100%;
      }
    }
  }
  /**/
  .box {
    opacity: 0.56;
    background: #000;
    z-index: 9;
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }
  .shareHint {
    width: 80%;
    margin: 0 auto;
    display: block;
    z-index: 999;
    position: fixed;
    top: 3rem;
    left: 3.3rem;
  }
  .progressOne {
    .splitY {
      height: 3rem;
      text-align: center;
      position: relative;
      .buttOne {
        width: 35%;
        height: 3rem;
        line-height: 3rem;
        position: absolute;
        font-size: 0.8rem;
        left: 50%;
        transform: translateX(-50%);
        top: 0;
        border: 1px solid #000;
        background-color: #fff;
        border-radius: 1.4rem;
        color: #000;
      }
    }
  }
}
</style>




